    z-index: -90;
    background-image: linear-gradient(to bottom right,#b2e673 40%, rgb(95, 233, 233) 100%,#d3f3ab 40%); 
    box-shadow : 0 0 50px #adf5f1, 0 0 50px #bdfcbd, 0 0 100px #f9fafa; 
    animation: animate 5s linear infinite;
}
.box_grid:hover{
 background-color: aqua;
}
@keyframes animate{
    0%
    {
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
